import { useQuery } from 'react-query';
import { Helmet } from 'react-helmet';
import {
  getPopularMovies,
  getNowPlayingMovies,
  getUpcomingMovies,
  IGetMoviesResult,
} from '../api';
import styled from 'styled-components';
import { BannerBox } from '../components/Banner';
import { Slide } from '../components/Slider';
import { Modal } from '../components/Modal';
import { useParams } from 'react-router-dom';

const Wrapper = styled.div`
  position: relative;
  background: black;
`;

const Loader = styled.div`
  font-size: 10vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

function Home() {
  //  
  const { data: popularData, isLoading: popularLoading } =
    useQuery<IGetMoviesResult>(['popularMovies', 'movies'], getPopularMovies);

  //   
  const { data: nowPlayingData, isLoading: nowPlayingLoading } =
    useQuery<IGetMoviesResult>(
      ['nowPlayingMovies', 'movies'],
      getNowPlayingMovies
    );

  //   
  const { data: upcomingData, isLoading: upcomingLoading } =
    useQuery<IGetMoviesResult>(['upcomingMovies', 'movies'], getUpcomingMovies);

  const isLoading =
    popularLoading || nowPlayingLoading || upcomingLoading || false;

  const params = useParams<{
    category: string | undefined;
    movieId: string | undefined;
  }>();

  return (
    <Wrapper>
      {isLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <Helmet>
            <title>Home | </title>
          </Helmet>

          {/*  */}
          <BannerBox
            id="movies"
            division="popularData"
            bannerData={popularData?.results}
          />

          {/*   */}
          <Slide
            id="popularData"
            division="movies"
            title=" "
            slideData={popularData?.results}
          />

          {/*    */}
          {/* <Slide
            id="nowPlayingData"
            division="movies"
            title="  "
            slideData={nowPlayingData?.results}
          /> */}

          {/*    */}
          {/* <Slide
            id="upcomingData"
            division="movies"
            title="  "
            slideData={upcomingData?.results}
          /> */}

          <Modal
            id="movies"
            division={String(params?.category)}
            modalData={popularData?.results}
          />
        </>
      )}
    </Wrapper>
  );
}
export default Home;
